#preview {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#print-btn {
    background: #dfe4e9;
    margin-bottom: 10px;
}

@media print {
    body * {
      visibility: hidden;
    }
    #final-preview, #final-preview * {
      visibility: visible;
    }
    #final-preview {
      position: absolute;
      left: 0;
      top: 0;
    }
  }